html {
  font: 400 16px / 1 'Roboto', sans-serif; }

.btn {
  background-color: #42A5F5;
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.15);
  -webkit-transition: box-shadow 0.2s ease-in-out, background-color 0.2s ease-in-out;
  transition: box-shadow 0.2s ease-in-out, background-color 0.2s ease-in-out;
  will-change: box-shadow;
  cursor: pointer; }
  .btn:hover {
    background-color: #64b5f6;
    box-shadow: 2px 5px 7px rgba(0, 0, 0, 0.25); }
  .btn.float {
    position: fixed;
    bottom: 2em;
    right: 2em;
    width: 3.5em;
    height: 3.5em;
    border-radius: 50%;
    border: 0; }
    .btn.float span {
      font-size: 3em;
      color: #fff; }
  .btn.btn-photo {
    width: 4.5em;
    height: 4.5em;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    background-color: #fff; }
    .btn.btn-photo span {
      font-size: 2.3em; }
    .btn.btn-photo:before {
      content: '';
      position: absolute;
      top: -0.2em;
      left: -0.2em;
      width: 4.75em;
      height: 4.75em;
      border: 1px solid #fff;
      border-radius: 50%; }

.card {
  display: flex;
  margin-bottom: 2em;
  position: relative;
  -webkit-perspective: 800px;
          perspective: 800px; }
  .card:hover .flip {
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg); }
  .card .flip {
    background-color: transparent;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: box-shadow 0.5s, -webkit-transform 0.5s;
    transition: box-shadow 0.5s, -webkit-transform 0.5s;
    transition: box-shadow 0.5s, transform 0.5s;
    transition: box-shadow 0.5s, transform 0.5s, -webkit-transform 0.5s; }
    .card .flip .face {
      -webkit-backface-visibility: hidden;
              backface-visibility: hidden;
      z-index: 2; }
    .card .flip .front {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1; }
    .card .flip .back {
      -webkit-transform: rotateY(-180deg);
              transform: rotateY(-180deg); }

.cards-wrapper {
  margin-top: 2em;
  display: flex;
  flex-direction: column;
  align-items: center; }

.camera {
  height: 100vh;
  background-color: #000;
  overflow: hidden; }

video {
  position: absolute;
  left: -9999px;
  opacity: 0;
  visibility: hidden;
  z-index: -9999; }

.line {
  height: 3px;
  margin: -3px 0 0;
  border: 0;
  background-color: #fff; }

.controls {
  position: relative; }
  .controls .float.btn-photo {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }

